import iSelect from "../../components/select/select.vue";
import iOption from "../../components/select/option.vue";
import Locale from "../../mixins/locale";
var prefixCls = 'ivu-page';

function isValueNumber(value) {
  return /^[1-9][0-9]*$/.test(value + '');
}

export default {
  name: 'PageOption',
  mixins: [Locale],
  components: {
    iSelect: iSelect,
    iOption: iOption
  },
  props: {
    pageSizeOpts: Array,
    showSizer: Boolean,
    showElevator: Boolean,
    current: Number,
    _current: Number,
    pageSize: Number,
    allPages: Number,
    isSmall: Boolean,
    placement: String,
    transfer: Boolean,
    disabled: Boolean
  },
  data: function data() {
    return {
      currentPageSize: this.pageSize
    };
  },
  watch: {
    pageSize: function pageSize(val) {
      this.currentPageSize = val;
    }
  },
  computed: {
    size: function size() {
      return this.isSmall ? 'small' : 'default';
    },
    optsClasses: function optsClasses() {
      return ["".concat(prefixCls, "-options")];
    },
    sizerClasses: function sizerClasses() {
      return ["".concat(prefixCls, "-options-sizer")];
    },
    ElevatorClasses: function ElevatorClasses() {
      return ["".concat(prefixCls, "-options-elevator")];
    }
  },
  methods: {
    changeSize: function changeSize() {
      this.$emit('on-size', this.currentPageSize);
    },
    changePage: function changePage(event) {
      var val = event.target.value.trim();
      var page = 0;

      if (isValueNumber(val)) {
        val = Number(val);

        if (val != this.current) {
          var allPages = this.allPages;

          if (val > allPages) {
            page = allPages;
          } else {
            page = val;
          }
        }
      } else {
        page = 1;
      }

      if (page) {
        this.$emit('on-page', page);
        event.target.value = page;
      }
    }
  }
};